.footer{
    z-index: -1;
    padding: 4rem;
    background: rgb(247, 245, 245);
    color: #9601a3;
    height: 45vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    text-align: center;
    margin: 0 auto; 
    font-family: 'Trebuchet HS', 'Lucida Sans Unicode','Lucida Grande',
    'Lucida Sans', Arial, sans-serif ;
    
}
.footer > p {
    font-size: 9px;
}

.footer > h1{
    font-size: 15px;
}


.footer-col{
    margin: 20px;
}

.all-rights{
    margin: 50px;
}

