video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}
.hero-container{
    /*background: url('../../../images/2.jpg') center center/cover no-repeat;*/
   
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    text-align: center;
    
}

.hero-container > h1{
 color: #fff;
 font-size: 100px;
 margin-top: -100px;
}

.hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 40px;
    font-family: 'Trebuchet HS', 'Lucida Sans Unicode','Lucida Grande',
    'Lucida Sans', Arial, sans-serif ;
}

.hero-btns{
    margin-top: 40px;
}

.hero-btns .btn{
   margin: 6px; 
}

.fa-play-circle{
    margin-left: 9px;
}

@media screen and (max-width: 960px) {
    .hero-container > h1{
        font-size: 80px;
        margin-top: -150px;
       }   
}

/*display these features if the screen size is minimized*/
@media screen and (max-width: 768px) {
    .hero-container > h1{
        font-size: 50px;
        margin-top: -100px;
       }  
       
       .hero-container > p{
           font-size: 30px;
           margin: 0 50px;
       }

       /*seperate the two buttons into two blocks*/
       .btn-mobile{
           display: block;
           text-decoration: none;
       }
       
       .btn{
           width: 100%;
       }
}